import { Component, OnInit } from "@angular/core";
import { FeatureNameModel } from "src/app/service/support/feature.name.model";
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { FeatureModel } from "src/app/service/support/feature.model";
import { MatSnackBar } from "@angular/material";
import { VehicleFeatureService } from "src/app/service/vehicle-feature.service";
import { AppSettings } from "src/app/service/support/app.settings";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-add-feature",
  templateUrl: "./add-feature.component.html",
  styleUrls: ["./add-feature.component.scss"]
})
export class AddFeatureComponent implements OnInit {
  featureName: FeatureNameModel = new FeatureNameModel();
  addFeatureNameForm: FormGroup;
  addFeatureValueForm: FormGroup;
  featureValue: FeatureModel = new FeatureModel();
  constructor(
    private spinner: NgxSpinnerService,
    private formBuilder: FormBuilder,
    private snackBar: MatSnackBar,
    private fatureService: VehicleFeatureService
  ) {
    this.featureName.Features = [];
  }

  ngOnInit() {
    this.addFeatureNameForm = this.formBuilder.group({
      featureNameControl: new FormControl("", [Validators.required]),
      featureNameDescriptionControl: new FormControl("")
    });
    this.addFeatureValueForm = this.formBuilder.group({
      featureValueControl: new FormControl("", [Validators.required]),
      featureValueDescription: new FormControl("")
    });

  }

  removeFeature(feature: FeatureModel) {
    const index = this.featureName.Features.indexOf(feature, 0);
    if (index > -1) {
      this.featureName.Features.splice(index, 1);
    }
  }
  addValue() {
    if (this.addFeatureValueForm.valid) {
      this.featureName.Features.push(this.featureValue);
      this.featureValue = new FeatureModel();
    } else {
      this.snackBar.open("Please enter at least value");
    }
  }
  save() {
    if (this.addFeatureNameForm.valid) {
      this.spinner.show();
      this.fatureService.addFeature(this.featureName).subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.featureName = new FeatureNameModel();
            this.featureName.Features = [];
            this.snackBar.open("Saved");
          } else {
            this.snackBar.open(response.ErrorMessage);
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
        }
      );
    } else {
      this.snackBar.open("Feature name is required");
    }
  }
}
