import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { CommonResponse } from './support/common.response';
import { ServerManager } from './support/server.manager';
import { AppSettings } from './support/app.settings';

@Injectable({
  providedIn: 'root'
})
export class CountryService {

  constructor(private http:HttpClient) { }
  getCountries():Observable<CommonResponse>{
    return this.http.get<CommonResponse>(ServerManager.getEndpoint(AppSettings.COUNTRIES_URL));
  }
}
