import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ServerManager } from './support/server.manager';
import { AppSettings } from './support/app.settings';
import { CommonResponse } from './support/common.response';

@Injectable({
  providedIn: 'root'
})
export class RegistrationStateService {

  constructor(private http:HttpClient) { }

  getAll():Observable<CommonResponse>{
    return this.http.get<CommonResponse>(ServerManager.getEndpoint(AppSettings.REGISTRATION_STATES_URL),ServerManager.getHeadders());
  }
}
