import { Component, OnInit, ViewChild } from "@angular/core";
import { MakeViewModel } from "src/app/service/support/make.view.model";
import { MakeService } from "src/app/service/make.service";
import { NgxSpinnerService } from "ngx-spinner";
import {
  MatTableDataSource,
  MatPaginator,
  MatSort,
  MatDialog,
  MatSnackBar
} from "@angular/material";
import { EditVehicleMakeComponent } from "./edit-vehicle-make/edit-vehicle-make.component";
import { AppSettings } from "src/app/service/support/app.settings";

@Component({
  selector: "app-vehical-make-mangement",
  templateUrl: "./vehical-make-mangement.component.html",
  styleUrls: ["./vehical-make-mangement.component.scss"]
})
export class VehicalMakeMangementComponent implements OnInit {
  tableData = new MatTableDataSource();
  displayedColumns: string[] = ["make", "description", "status", "edit"];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  newModel: MakeViewModel = new MakeViewModel();
  constructor(
    private spinner: NgxSpinnerService,
    private makeService: MakeService,
    public dialog: MatDialog,
    private snackBar: MatSnackBar
  ) {}
  openEditDialog(make: MakeViewModel): void {
    make.OldMakeName = make.MakeName;
    const dialogRef = this.dialog.open(EditVehicleMakeComponent, {
      width: "250px",
      data: make
    });

    dialogRef.afterClosed().subscribe(result => {
      this.fetchAllMakes();
    });
  }
  applyFilter(filterValue: string) {
    this.tableData.filter = filterValue.trim().toLowerCase();

    if (this.tableData.paginator) {
      this.tableData.paginator.firstPage();
    }
  }
  ngOnInit() {
    this.tableData.paginator = this.paginator;
    this.tableData.sort = this.sort;
    this.fetchAllMakes();
  }
  getStatusButtonState(state: string) {
    return state === "Active" ? true : false;
  }
  deleteMake(make:MakeViewModel){
    this.makeService.delete(make).subscribe(
      response => {
        if (response.Status) {
          this.snackBar.open(make.MakeName + " Deleted");
          this.fetchAllMakes();
        } else {
          this.snackBar.open(response.ErrorMessage);
          this.fetchAllMakes();
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        this.fetchAllMakes();
      }
    );
  }
  changeStateOfMake(state: boolean, model: MakeViewModel) {
    if (state) {
      this.makeService.active(model).subscribe(
        response => {
          if (response.Status) {
            this.snackBar.open(model.MakeName + " Activated");
            this.fetchAllMakes();
          } else {
            this.snackBar.open(response.ErrorMessage);
            this.fetchAllMakes();
          }
        },
        error => {
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          this.fetchAllMakes();
        }
      );
    } else {
      this.makeService.inactive(model).subscribe(
        response => {
          if (response.Status) {
            this.snackBar.open(model.MakeName + " Deactivated");
            this.fetchAllMakes();
          } else {
            this.snackBar.open(response.ErrorMessage);
            this.fetchAllMakes();
          }
        },
        error => {
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          this.fetchAllMakes();
        }
      );
    }
  }
  saveMake() {
    if(!this.newModel.MakeName){
      this.snackBar.open("Make name is required" );
      return;
    }
    this.spinner.show();
    this.makeService.addMake(this.newModel).subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.newModel = new MakeViewModel();
          this.fetchAllMakes();
        } else {
          this.snackBar.open(response.ErrorMessage);
        }
      },
      error => {
        this.spinner.hide();
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
      }
    );
  }
  fetchAllMakes() {
    this.spinner.show();
    this.makeService.getMakers().subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.tableData = new MatTableDataSource(response.Payload);
        } else {
          this.spinner.hide();
          console.log(response.ErrorMessage);
        }
      },
      error => {
        this.spinner.hide();
        console.log(error);
      }
    );
  }
}
