import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { CommonResponse } from './support/common.response';
import { ServerManager } from './support/server.manager';
import { AppSettings } from './support/app.settings';
@Injectable({
  providedIn: 'root'
})
export class StateService {

  constructor(private http: HttpClient) { }
  getStates(name:string): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(ServerManager.getEndpoint(AppSettings.GET_STATES_URL(name)));
  }
}
