import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { CommonResponse } from "./support/common.response";
import { ServerManager } from "./support/server.manager";
import { AppSettings } from "./support/app.settings";
import { VehicleModelViewModel } from "./support/vehicle.model.view.model";

@Injectable({
  providedIn: "root"
})
export class ModelService {
  constructor(private http: HttpClient) {}
  getModls(): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.GET_MODELS_URL),
      ServerManager.getHeadders()
    );
  }
  getActiveModls(): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.GET_ACTIVE_MODELS_URL),
      ServerManager.getHeadders()
    );
  }
  getMakeModls(makeId:string): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.MAKE_MODEL_URL(makeId)),
      ServerManager.getHeadders()
    );
  }
  add(model: VehicleModelViewModel) {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ADD_MODEL_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  update(model: VehicleModelViewModel) {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.UPDATE_MODEL_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  active(model: VehicleModelViewModel) {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ACTIVE_MODEL_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  inactive(model: VehicleModelViewModel) {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.INACTIVE_MODEL_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  delete(model: VehicleModelViewModel) {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.DELETE_MODEL_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  getModelSeries(model_id: string): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.MODEL_SERIES_URL(model_id)),
      ServerManager.getHeadders()
    );
  }
}
