import { Component, OnInit } from '@angular/core';
import { AppSettings } from 'src/app/service/support/app.settings';
import { GlobalEventService } from 'src/app/service/global.event.service';
import { JsLoader } from 'src/app/js-loader';

@Component({
  selector: 'app-site-home',
  templateUrl: './site-home.component.html',
  styleUrls: ['./site-home.component.scss']
})
export class SiteHomeComponent implements OnInit {

  constructor(private globalEventService:GlobalEventService) {
    localStorage.removeItem(AppSettings.AUTH_INFO_LOCATION);
    globalEventService.triggerRegresh();
    JsLoader.loadScript();
   }

  ngOnInit() {

  }

}
