import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { CommonResponse } from "./support/common.response";
import { ServerManager } from "./support/server.manager";
import { AppSettings } from "./support/app.settings";
import { MakeViewModel } from "./support/make.view.model";

@Injectable({
  providedIn: "root"
})
export class MakeService {
  constructor(private http: HttpClient) {}
  getMakers(): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.MAKERS_URL),
      ServerManager.getHeadders()
    );
  }
  getActiveMakers(): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ACTIVE_MAKERS_URL),
      ServerManager.getHeadders()
    );
  }
  addMake(make: MakeViewModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ADD_MAKE_URL),
      make,
      ServerManager.getHeadders()
    );
  }
  updateMake(make: MakeViewModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.UPDATE_MAKE_URL),
      make,
      ServerManager.getHeadders()
    );
  }
  active(make: MakeViewModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ACTIVE_MAKE_URL),
      make,
      ServerManager.getHeadders()
    );
  }
  inactive(make: MakeViewModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.INACTIVE_MAKE_URL),
      make,
      ServerManager.getHeadders()
    );
  }
  delete(make: MakeViewModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.DELETE_MAKE_URL),
      make,
      ServerManager.getHeadders()
    );
  }
}
