import { Component, OnInit } from "@angular/core";
import { StateService } from "src/app/service/state.service";
import { StateViewModel } from "src/app/service/support/state.view.model";
import { UserViewModel } from "src/app/service/support/user.view.model";
import {
  FormGroup,
  FormGroupDirective,
  FormControl,
  NgForm,
  FormBuilder,
  Validators
} from "@angular/forms";
import { ErrorStateMatcher } from "@angular/material/core";
import { UserService } from "src/app/service/user.service";
import { MembershipService } from "src/app/service/membership.service";
import { MembershipViewModel } from "src/app/service/support/membership.view.model";
import { Router } from "@angular/router";
import { NgxSpinnerComponent, NgxSpinnerService } from "ngx-spinner";
import { AppSettings } from "src/app/service/support/app.settings";
import { ConfirmPasswordErrorStateMatcher } from "src/app/service/support/error.state.matcher";
import { MatSnackBar } from '@angular/material';

@Component({
  selector: "app-register",
  templateUrl: "./register.component.html",
  styleUrls: ["./register.component.scss"]
})
export class RegisterComponent implements OnInit {
  registrationForm: FormGroup;
  matcher = new ConfirmPasswordErrorStateMatcher();
  states: StateViewModel[] = [];
  packages: MembershipViewModel[] = [];
  user: UserViewModel = new UserViewModel();
  confirmPassword: string;

  checkPasswords(group: FormGroup) {
    // here we have the 'passwords' group
    let pass = group.controls.password.value;
    let confirmPass = group.controls.confirmPassword.value;

    return pass === confirmPass ? null : { notSame: true };
  }

  constructor(
    private spinner: NgxSpinnerService,
    private router: Router,
    private stateService: StateService,
    private userService: UserService,
    private membershipService: MembershipService,
    private formBuilder: FormBuilder,
    private snackBar: MatSnackBar
  ) {
    this.user.CountryName = localStorage.getItem(AppSettings.COUNTRY_INFO);
    this.registrationForm = this.formBuilder.group(
      {
        membershipCombo: new FormControl("", Validators.required),
        username: new FormControl("", Validators.required),
        firstName: new FormControl("", [Validators.required]),
        lastName: new FormControl("", [Validators.required]),
        email: new FormControl("", [Validators.required, Validators.email]),
        mobileNumber: new FormControl("", [Validators.required]),
        orgName: new FormControl("", [Validators.required]),
        stateCombo: new FormControl("", [Validators.required]),
        password: ["", [Validators.required]],
        confirmPassword: [""]
      },
      { validator: this.checkPasswords }
    );
  }

  register() {
    if (this.registrationForm.hasError("notSame")) {
      return;
    }
    this.spinner.show();
    if (this.registrationForm.valid) {
      this.userService.registerUser(this.user).subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.snackBar.open("Registration successful, Activation email sent to you mail");
            this.router.navigate(["login"]);
          } else {
            this.snackBar.open(response.ErrorMessage);
          }
        },
        error => {
          console.log(error);
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          this.spinner.hide();
        }
      );
    } else {
      this.registrationForm.clearValidators();
    }
  }

  setState(x) {
    console.log("s");
  }
  ngOnInit() {
    this.spinner.show();
    this.stateService.getStates(localStorage.getItem(AppSettings.COUNTRY_INFO)).subscribe(
      response => {
        if (response.Status) {
          this.states = response.Payload;
          this.membershipService.getMemberships().subscribe(
            response => {
              this.spinner.hide();
              if (response.Status) {
                this.packages = response.Payload;
              }
            },
            error => {
              this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
              console.log(error);
              this.spinner.hide();
            }
          );
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }
    );
  }
}
