import { Component, OnInit, ViewChild, Inject } from "@angular/core";
import { DOCUMENT } from '@angular/common';
import { AppSettings } from "src/app/service/support/app.settings";
import { GlobalEventService } from "src/app/service/global.event.service";
import { UserService } from "src/app/service/user.service";
import { YardService } from "src/app/service/yard.service";
import { YardViewModel } from "src/app/service/support/yard.view.model";
import { Breakpoints } from "@angular/cdk/layout";
import { MatSidenav } from "@angular/material";
import { Router } from "@angular/router";
@Component({
  selector: "app-header",
  templateUrl: "./header.component.html",
  styleUrls: ["./header.component.scss"]
})
export class HeaderComponent implements OnInit {
  yards: YardViewModel[] = [];
  user = localStorage.getItem(AppSettings.AUTH_INFO_LOCATION);
  menuIcon = true;
  fullScreen = false;
  elem;

  constructor(
    private router: Router,
    private glovalEventService: GlobalEventService,
    private userService: UserService,
    private yardService: YardService,
    @Inject(DOCUMENT) private document: any

  ) {
    glovalEventService.onAppRefresh.subscribe(event => {
      switch (event) {
        case "reload":
          this.user = localStorage.getItem(AppSettings.AUTH_INFO_LOCATION);
          this.fetchYards();
          break;
      }
    });
    this.elem = document.documentElement;
  }
  toggleSideBar() {
    this.menuIcon = !this.menuIcon;
    this.glovalEventService.toggleSideNav();
  }

  toggleFullScreen()
  {
    this.fullScreen = !this.fullScreen;
    if(this.fullScreen)
    {
      if (this.elem.requestFullscreen) {
        this.elem.requestFullscreen();
      } else if (this.elem.mozRequestFullScreen) {
        /* Firefox */
        this.elem.mozRequestFullScreen();
      } else if (this.elem.webkitRequestFullscreen) {
        /* Chrome, Safari and Opera */
        this.elem.webkitRequestFullscreen();
      } else if (this.elem.msRequestFullscreen) {
        /* IE/Edge */
        this.elem.msRequestFullscreen();
      }
    }else{
      if (this.document.exitFullscreen) {
        this.document.exitFullscreen();
      } else if (this.document.mozCancelFullScreen) {
        /* Firefox */
        this.document.mozCancelFullScreen();
      } else if (this.document.webkitExitFullscreen) {
        /* Chrome, Safari and Opera */
        this.document.webkitExitFullscreen();
      } else if (this.document.msExitFullscreen) {
        /* IE/Edge */
        this.document.msExitFullscreen();
      }
    }
  }

  changeYard(yard:YardViewModel){
    AppSettings._SELECTEDYARD=yard;
    this.glovalEventService.onYardChange();
  }
  getSelectedYard(){
    return AppSettings._SELECTEDYARD;
  }
  ngOnInit() {}
  logout() {

    localStorage.clear();
    this.router.navigate(["/country"]);
  }
  canShow(){
    return this.user!=null &&(JSON.parse(this.user).UserRole!="Super User")
  }
  fetchYards() {
    if (localStorage.getItem(AppSettings.AUTH_INFO_LOCATION)) {
      this.yardService.getUserYards().subscribe(response => {
        if (response.Status) {
          this.yards = response.Payload;
          AppSettings._SELECTEDYARD= this.yards[0];
          this.glovalEventService.onYardChange();
        }
      });
    }
  }
}
