import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { CommonResponse } from "./support/common.response";
import { ServerManager } from "./support/server.manager";
import { AppSettings } from "./support/app.settings";

@Injectable({
  providedIn: "root"
})
export class CommonService {
  constructor(private http: HttpClient) {}

  filterVehiclesFromStatus(statusId: number,tab:number): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.FILTER_VEHICLES_URL(statusId,AppSettings._SELECTEDYARD.YardId,tab)),
      ServerManager.getHeadders()
    );
  }

  getVehicle(vehicleId:string): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.GET_VEHICLE_INFO_URL(vehicleId)),
      ServerManager.getHeadders()
    );
  }
}
