import { Component, OnInit, Inject } from "@angular/core";
import { MakeViewModel } from "src/app/service/support/make.view.model";
import { VehicalMakeMangementComponent } from "../vehical-make-mangement.component";
import {
  MatDialogRef,
  MatDialog,
  MAT_DIALOG_DATA,
  MatSnackBar
} from "@angular/material";
import {
  FormBuilder,
  FormGroup,
  FormControl,
  Validators
} from "@angular/forms";
import { MakeService } from "src/app/service/make.service";
import { NgxSpinnerService } from "ngx-spinner";
import { AppSettings } from "src/app/service/support/app.settings";

@Component({
  selector: "app-edit-vehicle-make",
  templateUrl: "./edit-vehicle-make.component.html",
  styleUrls: ["./edit-vehicle-make.component.scss"]
})
export class EditVehicleMakeComponent implements OnInit {
  editMakeForm: FormGroup;
  constructor(
    private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<VehicalMakeMangementComponent>,
    @Inject(MAT_DIALOG_DATA) public make: MakeViewModel,
    private formBuilder: FormBuilder,
    private makeService: MakeService,
    private spinner: NgxSpinnerService
  ) {}

  ngOnInit() {
    this.editMakeForm = this.formBuilder.group({
      makeNameControl: new FormControl("", [Validators.required]),
      makeDescriptionControl: new FormControl("")
    });
  }
  onCancelClick(): void {
    this.dialogRef.close();
  }
  onSaveClick(): void {
    if (this.editMakeForm.valid) {
      this.spinner.show();
      this.makeService.updateMake(this.make).subscribe(
        response => {
          if (response.Status) {
            this.dialogRef.close();
          } else {
            this.snackBar.open(response.ErrorMessage);
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
        }
      );
    } else {
      this.snackBar.open("Please fill all required fields");
    }
  }
}
