import { Component, OnInit } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { UserService } from "src/app/service/user.service";
import { NgxSpinnerService } from "ngx-spinner";
import { UserViewModel } from "src/app/service/support/user.view.model";
import { Router } from "@angular/router";
import { ConfirmPasswordErrorStateMatcher } from 'src/app/service/support/error.state.matcher';
import { MatSnackBar } from '@angular/material';
import { AppSettings } from 'src/app/service/support/app.settings';

@Component({
  selector: "app-do-recover-password",
  templateUrl: "./do-recover-password.component.html",
  styleUrls: ["./do-recover-password.component.scss"]
})
export class DoRecoverPasswordComponent implements OnInit {
  user: UserViewModel = new UserViewModel();
  confirmPassword: string;
  matcher = new ConfirmPasswordErrorStateMatcher();
  doRecoverFormGroup: FormGroup;
  constructor(
    private router: Router,
    private formBuilder: FormBuilder,
    private userService: UserService,
    private spinner: NgxSpinnerService,
    private snackBar: MatSnackBar
  ) {
    this.doRecoverFormGroup = this.formBuilder.group({
      userName: new FormControl("", [Validators.required]),
      password: new FormControl("", [Validators.required]),
      confirmPassword: new FormControl("", [Validators.required]),
      activationCode:new FormControl("",[Validators.required])
    },
    { validator: this.checkPasswords });
  }
  checkPasswords(group: FormGroup) {
    // here we have the 'passwords' group
    let pass = group.controls.password.value;
    let confirmPass = group.controls.confirmPassword.value;

    return pass === confirmPass ? null : { notSame: true };
  }
  doRecover() {
    if (this.doRecoverFormGroup.valid) {
      this.spinner.show();
      this.userService.doResetPassword(this.user).subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.snackBar.open("Password changed");
            this.router.navigate(["login"]);
          } else {
            this.snackBar.open(response.ErrorMessage);
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
        }
      );
    }
  }
  ngOnInit() {}
}
