import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { CommonResponse } from "./support/common.response";
import { ServerManager } from "./support/server.manager";
import { AppSettings } from "./support/app.settings";
import { UserViewModel } from "./support/user.view.model";
@Injectable({
  providedIn: "root"
})
export class UserService {
  constructor(private http: HttpClient) {}
  registerUser(user: UserViewModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.REGISTER_USER_URL),
      user
    );
  }
  login(user: UserViewModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.LOGIN_URL),
      user
    );
  }
  logout() {
    localStorage.removeItem(AppSettings.AUTH_INFO_LOCATION);
  }
  loginUserDetails(): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.LOGIN_USER_DETAILS),
      ServerManager.getHeadders()
    );
  }
  updateUserDetails(user: UserViewModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.UPDATE_USER_URL),
      user,
      ServerManager.getHeadders()
    );
  }
  resetPassword(user: UserViewModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.RESET_PASSWORD_URL),
      user,
      ServerManager.getHeadders()
    );
  }

  sendRecoverPasswordRequest(model: UserViewModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.RECOVER_PASSWORD_URL),
      model
    );
  }
  doResetPassword(model: UserViewModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.DO_RESET_PASSWORD_URL),
      model
    );
  }
  usersOfOrganization(): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.USERS_OF_ORG()),
      ServerManager.getHeadders()
    );
  }
}
