import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { CommonResponse } from './support/common.response';
import { AppSettings } from './support/app.settings';
import { ServerManager } from './support/server.manager';
@Injectable({
  providedIn: 'root'
})
export class MembershipService {

  constructor(private http: HttpClient) { }

  getMemberships(): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(ServerManager.getEndpoint(AppSettings.ALL_MEMBERSHIPS_URL));
  }
}
