import { Component, OnInit, Inject } from "@angular/core";
import { MAT_DIALOG_DATA, MatSnackBar } from "@angular/material";
import { FeatureNameModel } from "src/app/service/support/feature.name.model";
import {
  FormGroup,
  FormControl,
  FormBuilder,
  Validators
} from "@angular/forms";
import { FeatureModel } from "src/app/service/support/feature.model";
import { VehicleFeatureService } from "src/app/service/vehicle-feature.service";
import { AppSettings } from "src/app/service/support/app.settings";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-edit-feature",
  templateUrl: "./edit-feature.component.html",
  styleUrls: ["./edit-feature.component.scss"]
})
export class EditFeatureComponent implements OnInit {
  addFeatureNameForm: FormGroup;
  addFeatureValueForm: FormGroup;
  featureValue: FeatureModel = new FeatureModel();

  constructor(
    private spinner: NgxSpinnerService,
    private featureService: VehicleFeatureService,
    private formBuilder: FormBuilder,
    private snackBar: MatSnackBar,
    @Inject(MAT_DIALOG_DATA) public featureName: FeatureNameModel
  ) {}

  ngOnInit() {
    this.addFeatureNameForm = this.formBuilder.group({
      featureNameControl: new FormControl("", [Validators.required]),
      featureNameDescriptionControl: new FormControl("")
    });
    this.addFeatureValueForm = this.formBuilder.group({
      featureValueControl: new FormControl("", [Validators.required]),
      featureValueDescription: new FormControl("")
    });
  }
  update() {
    if (this.addFeatureNameForm.valid) {
      this.spinner.show();
      this.featureService.update(this.featureName).subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.snackBar.open("Updated");
            this.reloadUi();
          } else {
            this.snackBar.open(response.ErrorMessage);
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
        }
      );
    } else {
      this.snackBar.open("Feature name is required");
    }
  }
  deleteFeature(feature: FeatureModel) {
    this.spinner.show();
    this.featureService.deleteFeature(feature).subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.reloadUi();
        } else {
          this.snackBar.open(response.ErrorMessage);
        }
      },
      error => {
        this.spinner.hide();
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
      }
    );
  }
  getStatusButtonState(state: string) {
    return state === "Active" ? true : false;
  }
  addValue() {
    if (this.addFeatureValueForm.valid) {
      this.spinner.show();
      this.featureValue.FeatureNameId = this.featureName.FeatureNameId;
      this.featureService.addFeatureValue(this.featureValue).subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.featureName = response.Payload;
            this.reloadUi();
          } else {
            this.snackBar.open(response.ErrorMessage);
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
        }
      );
    } else {
      this.snackBar.open("Feature value is required");
    }
  }
  changeStateOfFeature(state: boolean, model: FeatureModel) {
    this.spinner.show();
    if (state) {
      this.featureService.activeFeature(model).subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.snackBar.open(model.FeatureValue + " Activated");
            this.reloadUi();
          } else {
            this.snackBar.open(response.ErrorMessage);
            this.reloadUi();
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
          this.reloadUi();
        }
      );
    } else {
      this.featureService.inactiveFeature(model).subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.snackBar.open(model.FeatureValue + " Deactivated");
            this.reloadUi();
          } else {
            this.snackBar.open(response.ErrorMessage);
            this.reloadUi();
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          this.reloadUi();
          console.log(error);
        }
      );
    }
  }
  reloadUi() {
    this.spinner.show();
    this.featureService
      .getFeatureName(this.featureName.FeatureNameId)
      .subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.featureName = response.Payload;
          } else {
            console.log(response.ErrorMessage);
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
        }
      );
  }
}
