import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import * as _moment from "moment";
import {
  DateAdapter,
  MAT_DATE_FORMATS,
  MAT_DATE_LOCALE,
  MatDatepicker,
  MatChipInputEvent,
  MatStepper,
  MatSnackBar
} from "@angular/material";
import { VehicleViewModel } from "src/app/service/support/vehicle.view.model";
import { CommonService } from "src/app/service/common.service";
import { AcquiredTypeService } from "src/app/service/acquired-type.service";
import { AcquiredTypeViewModel } from "src/app/service/support/acquired.type.view.model";
import { CostingType } from "src/app/service/support/costing.type.view.model";
import { CostingTypeService } from "src/app/service/costing-type.service";
import { VehicleService } from "src/app/service/vehicle.service";
import {
  UploadFile,
  UploadEvent,
  FileSystemFileEntry,
  FileSystemDirectoryEntry
} from "ngx-file-drop";
import { AppSettings } from "src/app/service/support/app.settings";
import { ServerManager } from "src/app/service/support/server.manager";
import { NgxSpinnerService } from "ngx-spinner";
const moment = _moment;
export const MY_FORMATS = {
  parse: {
    dateInput: "MM/YYYY"
  },
  display: {
    dateInput: "MM/YYYY",
    monthYearLabel: "MMM YYYY",
    dateA11yLabel: "LL",
    monthYearA11yLabel: "MMMM YYYY"
  }
};

@Component({
  selector: "app-view-vehicle",
  templateUrl: "./view-vehicle.component.html",
  styleUrls: ["./view-vehicle.component.scss"]
})
export class ViewVehicleComponent implements OnInit {
  public files: UploadFile[] = [];
  vehicleInfoForm: FormGroup;
  aquiredTypes: AcquiredTypeViewModel[] = [];
  acquisiionForm: FormGroup;
  vahicle: VehicleViewModel = new VehicleViewModel();
  section: number = 0;
  costingTypes: CostingType[] = [];
  images: string[] = [];
  advertisingForm: FormGroup;
  constructor(
    private route: ActivatedRoute,
    private snackBar: MatSnackBar,
    private spinner: NgxSpinnerService,
    private formBuilder: FormBuilder,
    private commonService: CommonService,
    private costingTypeService: CostingTypeService,
    private vehicleService: VehicleService,
    private aquiredTypeService: AcquiredTypeService
  ) {
    this.vehicleInfoForm = this.formBuilder.group({
      yearControl: new FormControl("", [Validators.required]),
      makeControl: new FormControl("", [Validators.required]),
      modleControl: new FormControl("", [Validators.required]),
      seriseControl: new FormControl(""),
      seriesControl: new FormControl(""),
      bodyTypeControl: new FormControl(""),
      transmissionControl: new FormControl(""),
      engineNumberControl: new FormControl(""),
      vinControl: new FormControl(""),
      buildDateControl: new FormControl(moment()),
      compliedControl: new FormControl(moment()),
      kmsControl: new FormControl("", [Validators.required]),
      advPriceControl: new FormControl("", [Validators.required]),
      minPriceControl: new FormControl(""),
      driveAwayControl: new FormControl(""),
      registrationStateControl: new FormControl("", [Validators.required]),
      registrationNumberControl: new FormControl(""),
      regExpControl: new FormControl(""),
      registrationSerialControl: new FormControl(""),
      yardControl: new FormControl("", [Validators.required]),
      formTypeControl: new FormControl("", [Validators.required]),
      conditionControl: new FormControl("", [Validators.required]),
      conditionRatingControl: new FormControl("", [Validators.required]),
      ketcordNumberControl: new FormControl(""),
      statusControl: new FormControl("", [Validators.required]),
      prevPriceControl: new FormControl(""),
      prevRegistrationControl: new FormControl(""),
      yearFirstRegistrationControl: new FormControl(""),
      suppliersStockNumberControl: new FormControl("")
    });
    this.acquisiionForm = formBuilder.group({
      dateAquired: new FormControl(Date.now),
      dateRegoCancel: new FormControl(""),
      warrentyExp: new FormControl(""),
      howAquire: new FormControl(""),
      purchasedBy: new FormControl("", [Validators.required]),
      tradedOnStockNumber: new FormControl(""),
      kmsTimeOfPurchase: new FormControl(""),
      purchasePrice: new FormControl("", [Validators.required]),
      load: new FormControl(""),
      intakeInstruction: new FormControl(""),
      hasEncumbrances: new FormControl(""),
      financeProvider: new FormControl(""),
      accountNumber: new FormControl(""),
      payout: new FormControl(""),
      ppsr: new FormControl(""),
      reportNumber: new FormControl(""),
      dateOfIssue: new FormControl(""),
      inspectorsLicence: new FormControl(""),
      fullName: new FormControl("", [Validators.required]),
      address: new FormControl(""),
      city: new FormControl(""),
      state: new FormControl(""),
      postCode: new FormControl(""),
      country: new FormControl(""),
      mobile: new FormControl(""),
      phone: new FormControl(""),
      workPhone: new FormControl(""),
      email: new FormControl("", [Validators.email]),
      driversLicence: new FormControl(""),
      dateOfBirth: new FormControl(""),
      advertisingSource: new FormControl(""),
      abn: new FormControl(""),
      dealerLicence: new FormControl(""),
      govCrn: new FormControl(""),
      vehicleHasBeenWrittenOff: new FormControl(""),
      suspectOdometerHasBeenALterd: new FormControl(""),
      damageCouseByWater: new FormControl(""),
      hasBeenModified: new FormControl(""),
      clothinOffPeriodApplicable: new FormControl(""),
      statutoryGaurateeApplicable: new FormControl(""),
      exHireVehicle: new FormControl(""),
      kmsWrongBy: new FormControl(""),
      otherDefects: new FormControl("")
    });
    this.advertisingForm = formBuilder.group({
      title: new FormControl(""),
      description: new FormControl(""),
      shortDescription: new FormControl(""),
      tags: new FormControl("")
    });
  }
  fethcImages() {
    let id = this.route.snapshot.paramMap.get("id");
    this.spinner.show();
    this.vehicleService.getVehicleImages(id).subscribe(
      res => {
        this.spinner.hide();
        if (res.Status) {
          this.images = res.Payload;
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }
    );
  }
  getImageUrl(image) {
    return ServerManager.getEndpoint("/images/" + image);
  }
  public dropped(event: UploadEvent) {
    this.files = event.files;
    for (const droppedFile of event.files) {
      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          // Here you can access the real file
          console.log(droppedFile.relativePath, file);

          // You could upload it like this:
          const formData = new FormData();
          formData.append(file.name, file);

          let id = this.route.snapshot.paramMap.get("id");
          this.spinner.show();
          this.vehicleService.uploadVehicleImage(id, formData).subscribe(
            res => {
              this.spinner.hide();
              this.snackBar.open("Image uploaded");
            },
            error => {
              this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
              console.log(error);
              this.spinner.hide();
            }
          );
        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
        console.log(droppedFile.relativePath, fileEntry);
      }
    }
  }

  updateVahicle() {
    this.spinner.show();
    this.vehicleService.updateVehicle(this.vahicle).subscribe(
      res => {
        this.spinner.hide();
        if (res.Status) {
          this.snackBar.open("Updated");
        } else {
          this.snackBar.open(res.ErrorMessage);
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }
    );
  }
  ngOnInit() {
    this.spinner.show();
    this.aquiredTypeService.getAll().subscribe(
      res => {
        this.spinner.hide();
        if (res.Status) {
          this.aquiredTypes = res.Payload;
          this.costingTypeService.getAll().subscribe(
            res => {
              if (res.Status) {
                this.costingTypes = res.Payload;
                this.getVehicleDetails();
              }
            },
            error => {
              this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
              console.log(error);
              this.spinner.hide();
            }
          );
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }
    );
  }
  getVehicleDetails() {
    this.spinner.show();
    let id = this.route.snapshot.paramMap.get("id");
    this.commonService.getVehicle(id).subscribe(
      res => {
        this.spinner.hide();
        if (res.Status) {
          this.vahicle = res.Payload;
          this.fethcImages();
        } else {
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }
    );
  }
}
