import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { CommonResponse } from './support/common.response';
import { ServerManager } from './support/server.manager';
import { AppSettings } from './support/app.settings';

@Injectable({
  providedIn: 'root'
})
export class ConditionRatingService {

  constructor(private http:HttpClient) { }
  getRatings():Observable<CommonResponse>{
    return this.http.get<CommonResponse>(ServerManager.getEndpoint(AppSettings.CONDITION_RATING_URL),ServerManager.getHeadders());
  }
}
