import { Component, OnInit, ViewChild } from "@angular/core";
import { GlobalEventService } from "src/app/service/global.event.service";
import { MatSidenav } from "@angular/material";

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.scss"]
})
export class HomeComponent implements OnInit {
  @ViewChild("sidenav") sidenav: MatSidenav;
  constructor(private globalEventService: GlobalEventService) {
    globalEventService.onAppRefresh.subscribe(event => {
      switch (event) {
        case "toggle-side-nav":
          this.sidenav.toggle();
          break;
      }
    });
  }

  ngOnInit() {
    this.globalEventService.triggerRegresh();
    this.sidenav.open();
  }
}
