import { AppSettings } from "./app.settings";
import { HttpHeaders } from "@angular/common/http";

export class ServerManager {
  static getEndpoint(endpoint: string) {
    return AppSettings.SERVER_ROOT + endpoint.toString();
  }
  static getHeadders() {

    return {
      headers: new HttpHeaders({
        "Content-Type": "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem(AppSettings.AUTH_INFO_LOCATION)).AuthToken
      })
    };
  }
}
