import { EventEmitter } from "@angular/core";

export class GlobalEventService{
  onAppRefresh:EventEmitter<String>;
  constructor(){
    this.onAppRefresh=new EventEmitter();
  }
  triggerRegresh(){
    this.onAppRefresh.emit("reload");
  }
  toggleSideNav(){
    this.onAppRefresh.emit("toggle-side-nav");
  }
  onYardChange(){
    this.onAppRefresh.emit("on-yard_change");
  }
}
