import { Component, OnInit, Inject } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from "@angular/forms";
import { MakeViewModel } from "src/app/service/support/make.view.model";
import { MakeService } from "src/app/service/make.service";
import { MatSnackBar, MatDialogRef, MAT_DIALOG_DATA } from "@angular/material";
import { AppSettings } from "src/app/service/support/app.settings";
import { VehicleModelManagementComponent } from "../vehicle-model-management.component";
import { VehicleModelViewModel } from "src/app/service/support/vehicle.model.view.model";
import { NgxSpinnerService } from "ngx-spinner";
import { ModelService } from "src/app/service/model.service";

@Component({
  selector: "app-edit-vehicle-model",
  templateUrl: "./edit-vehicle-model.component.html",
  styleUrls: ["./edit-vehicle-model.component.scss"]
})
export class EditVehicleModelComponent implements OnInit {
  slectedMaker: MakeViewModel = null;
  editModelFormGroup: FormGroup;
  makers: MakeViewModel[] = [];
  constructor(
    private spinner: NgxSpinnerService,
    private modelService: ModelService,
    formBuilder: FormBuilder,
    private makeService: MakeService,
    private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<VehicleModelManagementComponent>,
    @Inject(MAT_DIALOG_DATA) public model: VehicleModelViewModel
  ) {
    console.log(model);
    this.editModelFormGroup = formBuilder.group({
      modelNameControl: new FormControl("", [Validators.required]),
      modelDescriptionControl: new FormControl(""),
      makeControl: new FormControl("", [Validators.required])
    });
  }
  onCancelClick(): void {
    this.dialogRef.close();
  }
  onSaveClick(): void {
    if (this.editModelFormGroup.valid) {
      this.spinner.show();
      this.modelService.update(this.model).subscribe(
        response => {
          if (response.Status) {
            this.dialogRef.close();
          } else {
            this.snackBar.open(response.ErrorMessage);
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
        }
      );
    } else {
      this.snackBar.open("Please fill all required fields");
    }
  }
  ngOnInit() {
    this.makeService.getActiveMakers().subscribe(
      response => {
        if (response.Status) {
          this.makers = response.Payload;
          this.makers.forEach(m => {
            if (m.MakeId == this.model.Make.MakeId) {
              this.model.Make=m;
            }
          });
        } else {
          this.snackBar.open(response.ErrorMessage);
        }
      },
      error => {
        console.log(error);
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
      }
    );
  }
}
