import { Component, OnInit, ViewChild } from "@angular/core";
import { ActivatedRoute, Router, NavigationEnd } from "@angular/router";
import { CommonService } from "src/app/service/common.service";
import { NgxSpinnerService } from "ngx-spinner";
import { GlobalEventService } from "src/app/service/global.event.service";
import { AppSettings } from "src/app/service/support/app.settings";
import { async } from "@angular/core/testing";
import { NestedTreeControl, FlatTreeControl } from "@angular/cdk/tree";
import {
  MatTreeFlattener,
  MatTreeFlatDataSource,
  Sort,
  MatPaginator,
  MatSort,
  MatTableDataSource,
  MatSnackBar
} from "@angular/material";
import { stringify } from "@angular/core/src/render3/util";
import { ServerManager } from 'src/app/service/support/server.manager';
export class TreeNode {
  name: string;
  children?: TreeNode[];
}
export class ExampleFlatNode {
  expandable: boolean;
  name: string;
  level: number;
}
@Component({
  selector: "app-on-offer",
  templateUrl: "./on-offer.component.html",
  styleUrls: ["./on-offer.component.scss"]
})
export class OnOfferComponent implements OnInit {
  statusId: string = "2";
  title: string;
  totalVehicles: number;
  avgValue: number;
  totalValue: number;
  tab: number = 0;

  vahiclesMap: Map<string, Map<string, any[]>> = new Map<
    string,
    Map<string, any[]>
  >();
  vehicles: any[] = [];
  displayedColumns: string[] = [
    "stock_number",
    "photo",
    "model",
    "price",
    "action"
  ];
  tableData: MatTableDataSource<any>;
  private transformer = (node: TreeNode, level: number) => {
    return {
      expandable: !!node.children && node.children.length > 0,
      name: node.name,
      level: level
    };
  };

  treeControl = new FlatTreeControl<ExampleFlatNode>(
    node => node.level,
    node => node.expandable
  );

  treeFlattener = new MatTreeFlattener(
    this.transformer,
    node => node.level,
    node => node.expandable,
    node => node.children
  );

  dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
  hasChild = (_: number, node: ExampleFlatNode) => node.expandable;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private commonService: CommonService,
    private spinner: NgxSpinnerService,
    private glovalEventService: GlobalEventService,
    private snackBar:MatSnackBar
  ) {
    router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.loadVahicles();
      }
    });
    glovalEventService.onAppRefresh.subscribe(event => {
      switch (event) {
        case "on-yard_change":
          this.loadVahicles();
          break;
      }
    });
  }
  changeTab(event) {
    this.tab = event.index;
    this.loadVahicles();
  }
  getFirstImage(images: string[]) {
    if (images.length > 0) return ServerManager.getEndpoint("/images/" + images[0]);
    else return "";
  }
  ngOnInit() {
    this.tableData.paginator = this.paginator;
    this.tableData.sort = this.sort;
  }
  filterVehicles(vmake?: string, vmodel?: string) {
    this.vehicles = [];
    if (vmake == null && vmodel == null) {
      Array.from(this.vahiclesMap.keys()).forEach(make => {
        Array.from(this.vahiclesMap.get(make).keys()).forEach(model => {
          this.vahiclesMap
            .get(make)
            .get(model)
            .forEach(vehicle => {
              vehicle.forEach(v => {
                this.vehicles.push(v);
              });
            });
        });
      });
    }
    if (vmake != null && vmodel == null) {
      Array.from(this.vahiclesMap.get(vmake).keys()).forEach(model => {
        this.vahiclesMap
          .get(vmake)
          .get(model)
          .forEach(vehicle => {
            vehicle.forEach(v => {
              this.vehicles.push(v);
            });
          });
      });
    }
    if (vmake != null && vmodel != null) {
      this.vahiclesMap
        .get(vmake)
        .get(vmodel)
        .forEach(vehicle => {
          vehicle.forEach(v => {
            vehicle.forEach(v => {
              this.vehicles.push(v);
            });
          });
        });
    }
    this.tableData = new MatTableDataSource(this.vehicles);
    this.totalVehicles = this.vehicles.length;

    this.totalValue = 0;
    this.vehicles.forEach(v => {
      this.totalValue += v.Value;
    });
    this.avgValue = this.totalValue / this.vehicles.length;
  }
  loadVahicles() {
    this.spinner.show();
    if (!AppSettings._SELECTEDYARD) this.spinner.hide();
    this.dataSource = new MatTreeFlatDataSource(
      this.treeControl,
      this.treeFlattener
    );
    this.vahiclesMap.clear();
    this.commonService
      .filterVehiclesFromStatus(Number(this.statusId), this.tab)
      .subscribe(
        res => {
          this.spinner.hide();
          if (res.Status) {
            let TREE_DATA: TreeNode[] = [];
            Object.keys(res.Payload).forEach(make => {
              let makeNode: TreeNode = { name: make, children: [] };
              let modelVehicles: Map<string, any[]> = new Map<string, any[]>();
              Object.keys(res.Payload[make]).forEach(model => {
                let modelNode: TreeNode = { name: model };
                makeNode.children.push(modelNode);
                if (!modelVehicles.has(model)) modelVehicles.set(model, []);
                modelVehicles.get(model).push(res.Payload[make][model]);
              });
              TREE_DATA.push(makeNode);
              this.dataSource.data = TREE_DATA;

              this.vahiclesMap.set(make, modelVehicles);
            });
            this.filterVehicles(null, null);
          } else {
            console.log(res.ErrorMessage);
          }
        },
        error => {
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
          this.spinner.hide();
        }
      );
  }
  applyFilter(filterValue: string) {
    this.tableData.filter = filterValue.trim().toLowerCase();

    if (this.tableData.paginator) {
      this.tableData.paginator.firstPage();
    }
  }
}
