import { Component, OnInit } from "@angular/core";
import { VehicleType } from "src/app/service/support/vehicle.type";
import { Route, Router, ActivatedRoute } from "@angular/router";
import { ServerManager } from 'src/app/service/support/server.manager';

@Component({
  selector: "app-vehicle-type",
  templateUrl: "./vehicle-type.component.html",
  styleUrls: ["./vehicle-type.component.scss"]
})
export class VehicleTypeComponent implements OnInit {
  vehicleTypes = VehicleType;

  constructor(private router: Router, private route: ActivatedRoute) {}

  ngOnInit() {}

  setVehicleType(type: VehicleType) {
    this.router.navigate(["../add-vehicle"], { relativeTo: this.route,queryParams:{type:type.valueOf()} });
  }
  getFirstImage(images: string[]) {
    if (images.length > 0) return ServerManager.getEndpoint("/images/" + images[0]);
    else return "";
  }
}
