  private _filterModel(value: string): VehicleModelViewModel[] {
    const filterValue = value.toLowerCase();
    return this.models.filter(
      state => state.ModelName.toLowerCase().indexOf(filterValue) === 0
    );
  }
  private _filterBody(value: string): VehicleTypeFeatureValueViewModel[] {
    const filterValue = value.toLowerCase();
    var x: VehicleTypeFeatureValueViewModel[] = this.typeFeatures.Body;
    return x.filter(
      state => state.FeatureValue.toLowerCase().indexOf(filterValue) === 0
    );
  }

  private _filterTansmissionControl(
    value: string
  ): VehicleTypeFeatureValueViewModel[] {
    const filterValue = value.toLowerCase();
    var x: VehicleTypeFeatureValueViewModel[] = this.typeFeatures.Transmission;
    return x.filter(
      state => state.FeatureValue.toLowerCase().indexOf(filterValue) === 0
    );
  }
  private _filterSeries(value: string): ModelSeriesViewModel[] {
    const filterValue = value.toString().toLowerCase();
    return this.modelSeries.filter(
      state =>
        state.Name.toString()
          .toLowerCase()
          .indexOf(filterValue) === 0
    );
  }
