import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { FeatureNameModel } from "./support/feature.name.model";
import { Observable } from "rxjs";
import { CommonModule } from "@angular/common";
import { CommonResponse } from "./support/common.response";
import { ServerManager } from "./support/server.manager";
import { AppSettings } from "./support/app.settings";
import { FeatureModel } from "./support/feature.model";

@Injectable({
  providedIn: "root"
})
export class VehicleFeatureService {
  constructor(private http: HttpClient) {}

  addFeature(model: FeatureNameModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ADD_FEATURE_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  getAllFeatureNames(): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ALL_FEATURE_URL),
      ServerManager.getHeadders()
    );
  }
  active(model: FeatureNameModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ACTIVE_FEATURE_NAME_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  inactive(model: FeatureNameModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.INACTIVE_FEATURE_NAME_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  delete(model: FeatureNameModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.DELETE_FEATURE_NAME_URL),
      model,
      ServerManager.getHeadders()
    );
  }

  deleteFeature(model: FeatureModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.DELETE_FEATURE_VALUE_URL),
      model,
      ServerManager.getHeadders()
    );
  }

  activeFeature(model: FeatureModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ACTIVE_FEATURE_VALUE_NAME_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  inactiveFeature(model: FeatureModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.INACTIVE_FEATURE_VALUE_NAME_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  getFeatureName(id: string): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.FEATURE_NAME_FROM_ID_URL(id)),
      ServerManager.getHeadders()
    );
  }
  addFeatureValue(feature: FeatureModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ADD_FEATURE_VALUE_URL),
      feature,
      ServerManager.getHeadders()
    );
  }
  update(model: FeatureNameModel): Observable<CommonResponse> {
    return this.http.put<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.UPDATE_FEATURE__NAME_URL),
      model,
      ServerManager.getHeadders()
    );
  }
}
