export class JsLoader {
    public static loadScript() {        
        var isFound = false;
        var scripts = document.getElementsByTagName("script")
        for (var i = 0; i < scripts.length; ++i) {
            if (scripts[i].getAttribute('src') != null && scripts[i].getAttribute('src').includes("loader")) {
                isFound = true;
            }
        }
    
        if (!isFound) {
            var dynamicScripts = ["assets/js/jquery-2.1.4.min.js",
            "assets/js/bootstrap.min.js",
            "assets/js/jquery.flexslider-min.js",
            "assets/js/jquery.appear.js",
            "assets/js/jquery.plugin.js",
            "assets/js/jquery.countdown.js",
            "assets/js/jquery.waypoints.min.js",
            "assets/js/jquery.validate.min.js",
            "assets/js/toastr.min.js",
            "assets/js/startuply.js"];
    
            for (var i = 0; i < dynamicScripts .length; i++) {
                let node = document.createElement('script');
                node.src = dynamicScripts [i];
                node.type = 'text/javascript';
                node.async = false;
                node.charset = 'utf-8';
                document.getElementsByTagName('head')[0].appendChild(node);
            }
    
        }
    }
}