import { Injectable } from "@angular/core";
import { HttpClient, HttpRequest } from "@angular/common/http";
import { VehicleViewModel } from "./support/vehicle.view.model";
import { Observable } from "rxjs";
import { CommonResponse } from "./support/common.response";
import { ServerManager } from "./support/server.manager";
import { AppSettings } from "./support/app.settings";
import { CommaExpr } from "@angular/compiler";

@Injectable({
  providedIn: "root"
})
export class VehicleService {
  constructor(private http: HttpClient) {}

  addVehicle(model: VehicleViewModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.ADD_VEHICLE_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  updateVehicle(model: VehicleViewModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.UPDATE_VEHICLE_URL),
      model,
      ServerManager.getHeadders()
    );
  }
  uploadVehicleImage(vehicleId: string, data: FormData) {

    const req = new HttpRequest('POST', ServerManager.getEndpoint(
      AppSettings.UPLOAD_VEHICLE_IMAGE_URL(vehicleId)
    ), data);
    return this.http.request(req);

  }
  getVehicleImages(vehicleId:string):Observable<CommonResponse>{
    return this.http.get<CommonResponse>(ServerManager.getEndpoint(AppSettings.VEHICLE_IMAGES_URL(vehicleId)),ServerManager.getHeadders());
  }
}
