import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { CommonResponse } from "./support/common.response";
import { Observable } from "rxjs/internal/Observable";
import { ServerManager } from "./support/server.manager";
import { AppSettings } from "./support/app.settings";

@Injectable({
  providedIn: "root"
})
export class VehicleTypeFeatureService {
  constructor(private http: HttpClient) {}
  getVehicleTypeFeatures(type: string): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.GET_TYPE_FEATURES(type)),
      ServerManager.getHeadders()
    );
  }
}
