import { Component, OnInit } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { UserViewModel } from "src/app/service/support/user.view.model";
import { UserService } from "src/app/service/user.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Route } from "@angular/compiler/src/core";
import { Router } from "@angular/router";
import { MatSnackBar } from '@angular/material';
import { AppSettings } from 'src/app/service/support/app.settings';

@Component({
  selector: "app-recover-password",
  templateUrl: "./recover-password.component.html",
  styleUrls: ["./recover-password.component.scss"]
})
export class RecoverPasswordComponent implements OnInit {
  user: UserViewModel = new UserViewModel();
  recoverFormGroup: FormGroup;
  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private userService: UserService,
    private spinner: NgxSpinnerService,
    private snackBar: MatSnackBar
  ) {
    this.recoverFormGroup = this.formBuilder.group({
      userName: new FormControl("", [Validators.required])
    });
  }
  recover() {
    if (this.recoverFormGroup.valid) {
      this.spinner.show();
      this.userService.sendRecoverPasswordRequest(this.user).subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.snackBar.open("Recover code sent to your email");
            this.router.navigate(["do-recover-password"]);
          } else {
            this.snackBar.open(response.ErrorMessage);
          }
        },
        error => {
          this.spinner.hide();
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          console.log(error);
        }
      );
    } else {
      this.snackBar.open("Please enter username or email address");
    }
  }
  ngOnInit() {}
}
