import { Injectable } from "@angular/core";
import {
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  CanActivate,
  Router
} from "@angular/router";
import { AppSettings } from "../service/support/app.settings";

@Injectable({
  providedIn: "root"
})
export class LoginGuard implements CanActivate {
  constructor(private router: Router) {
    localStorage.removeItem(AppSettings.AUTH_INFO_LOCATION);
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    if (!localStorage.getItem(AppSettings.COUNTRY_INFO)) {
      this.router.navigate(["/country"]);
      return false;
    }
    if (!localStorage.getItem(AppSettings.AUTH_INFO_LOCATION)) {
      return true;
    }
    this.router.navigate(["/"]);
    return false;
  }
}
