import { Component, OnInit } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { YardViewModel } from "src/app/service/support/yard.view.model";
import { StateViewModel } from "src/app/service/support/state.view.model";
import { StateService } from "src/app/service/state.service";
import { NgxSpinnerService } from "ngx-spinner";
import { CountryViewModel } from "src/app/service/support/country.view.model";
import { CountryService } from "src/app/service/country.service";
import { YardService } from "src/app/service/yard.service";
import { AppSettings } from "src/app/service/support/app.settings";
import { MatSnackBar } from '@angular/material';

@Component({
  selector: "app-add-yard",
  templateUrl: "./add-yard.component.html",
  styleUrls: ["./add-yard.component.scss"]
})
export class AddYardComponent implements OnInit {
  yard: YardViewModel = new YardViewModel();
  states: StateViewModel[] = [];
  countries: CountryViewModel[] = [];
  addYardForm: FormGroup;
  constructor(
    private formBuilder: FormBuilder,
    private stateService: StateService,
    private spinner: NgxSpinnerService,
    private countryService: CountryService,
    private yardService: YardService,
    private snackBar: MatSnackBar
  ) {
    this.addYardForm = this.formBuilder.group({
      yardName: new FormControl("", [Validators.required]),
      stateCombo: new FormControl("", [Validators.required]),
      yardCode: new FormControl("", [Validators.required]),
      yardAddress: new FormControl("", [Validators.required]),
      yardSuburb: new FormControl("", [Validators.required]),
      yardPostCode: new FormControl("", [Validators.required]),
      countryCombo: new FormControl("", [Validators.required]),
      yardPhone: new FormControl("", [Validators.required]),
      yardFax: new FormControl(""),
      yardEmail: new FormControl("", [Validators.required, Validators.email]),
      yardAbn: new FormControl("", [Validators.required]),
      yardDealerLicenceNumber: new FormControl("", [Validators.required]),
      yardDealerLicenceExpiry: new FormControl(""),
      yardLatitude: new FormControl(""),
      yardLongitude: new FormControl(""),
      yardRmsCrnZnta: new FormControl(""),
      yardWebsite: new FormControl(""),
      yardLocationCode: new FormControl(""),
      yardTradingHours: new FormControl(""),
      yardRegisterdBusinessName: new FormControl(""),
      yardUseDriveAwayPricing: new FormControl(""),
      yardDefaultDriveAwayPricing: new FormControl(""),
      yardCalcStampDuty: new FormControl(""),
      yardUserVinLookup: new FormControl(""),
      yardEnableBuybackTab: new FormControl(""),
      yardStockNumberBaseOnYard: new FormControl(""),
      yardPartNumberBaseOnYard: new FormControl(""),
      yadTaxInvoceTerms: new FormControl(""),
      yardInspectionReportUrl: new FormControl("")
    });
  }
  add() {
    this.spinner.show();
    this.yard.YardDealerLicenceExpiry = this.yard.YardDealerLicenceExpiry
      ? new Date(this.yard.YardDealerLicenceExpiry.toString()).toDateString()
      : null;
    this.yardService.addYard(this.yard).subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.snackBar.open("Saved");
        } else {
          this.snackBar.open(response.ErrorMessage);
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }
    );
  }
  ngOnInit() {
    this.spinner.show();
    this.stateService.getStates(localStorage.getItem(AppSettings.COUNTRY_INFO)).subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.states = response.Payload;
        }
      },
      error => {
        console.log(error);
        this.spinner.hide();
      }
    );
  }
}
