import { Component, OnInit, ViewChild } from "@angular/core";
import {
  MatTableDataSource,
  MatSort,
  MatPaginator,
  MatSnackBar,
  MatDialog
} from "@angular/material";
import { ModelService } from "src/app/service/model.service";
import { VehicleModelViewModel } from "src/app/service/support/vehicle.model.view.model";
import { NgxSpinnerService } from "ngx-spinner";
import { MakeViewModel } from "src/app/service/support/make.view.model";
import { MakeService } from "src/app/service/make.service";
import { AppSettings } from "src/app/service/support/app.settings";
import { EditVehicleModelComponent } from "./edit-vehicle-model/edit-vehicle-model.component";

@Component({
  selector: "app-vehicle-model-management",
  templateUrl: "./vehicle-model-management.component.html",
  styleUrls: ["./vehicle-model-management.component.scss"]
})
export class VehicleModelManagementComponent implements OnInit {
  tableData = new MatTableDataSource();
  makers: MakeViewModel[] = [];
  displayedColumns: string[] = [
    "name",
    "description",
    "make",
    "status",
    "edit"
  ];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  newModel: VehicleModelViewModel = new VehicleModelViewModel();
  constructor(
    private modelService: ModelService,
    private spinner: NgxSpinnerService,
    public dialog: MatDialog,
    private snackBar: MatSnackBar,
    private makeService: MakeService
  ) {}
  applyFilter(filterValue: string) {
    this.tableData.filter = filterValue.trim().toLowerCase();

    if (this.tableData.paginator) {
      this.tableData.paginator.firstPage();
    }
  }
  ngOnInit() {
    this.tableData.paginator = this.paginator;
    this.tableData.sort = this.sort;
    this.fetchAllModels();
    this.fetchMakers();
  }
  changeStateOfModel(state: boolean, model: VehicleModelViewModel) {
    if (state) {
      this.modelService.active(model).subscribe(
        response => {
          if (response.Status) {
            this.snackBar.open(model.ModelName + " Activated");
            this.fetchAllModels();
          } else {
            this.snackBar.open(response.ErrorMessage);
            this.fetchAllModels();
          }
        },
        error => {
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          this.fetchAllModels();
        }
      );
    } else {
      this.modelService.inactive(model).subscribe(
        response => {
          if (response.Status) {
            this.snackBar.open(model.ModelName + " Deactivated");
            this.fetchAllModels();
          } else {
            this.snackBar.open(response.ErrorMessage);
            this.fetchAllModels();
          }
        },
        error => {
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          this.fetchAllModels();
        }
      );
    }
  }
  openEditDialog(model: MakeViewModel): void {
    const dialogRef = this.dialog.open(EditVehicleModelComponent, {
      width: "250px",
      data: model
    });
    dialogRef.afterClosed().subscribe(result => {
      this.fetchAllModels();
    });
  }
  deleteModel(model: VehicleModelViewModel) {
    this.modelService.delete(model).subscribe(
      response => {
        if (response.Status) {
          this.snackBar.open(model.ModelName + " Deleted");
          this.fetchAllModels();
        } else {
          this.snackBar.open(response.ErrorMessage);
          this.fetchAllModels();
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        this.fetchAllModels();
      }
    );
  }
  saveModel() {
    if (!this.newModel.ModelName || !this.newModel.Make) {
      this.snackBar.open("Model name and maker is required");
      return;
    }
    this.spinner.show();
    this.modelService.add(this.newModel).subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.newModel = new VehicleModelViewModel();
          this.fetchAllModels();
        } else {
          this.snackBar.open(response.ErrorMessage);
        }
      },
      error => {
        this.spinner.hide();
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
      }
    );
  }
  fetchMakers() {
    this.spinner.show();
    this.makeService.getActiveMakers().subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.makers = response.Payload;
        } else {
          this.spinner.hide();
          console.log(response.ErrorMessage);
        }
      },
      error => {
        this.spinner.hide();
        console.log(error);
      }
    );
  }
  fetchAllModels() {
    this.spinner.show();
    this.modelService.getModls().subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.tableData = new MatTableDataSource(response.Payload);
        } else {
          this.spinner.hide();
          console.log(response.ErrorMessage);
        }
      },
      error => {
        this.spinner.hide();
        console.log(error);
      }
    );
  }
  getStatusButtonState(state: string) {
    return state === "Active" ? true : false;
  }
}
