import { Component, OnInit, ViewChild } from "@angular/core";
import { MatTableDataSource, MatSnackBar, MatDialog, MatPaginator, MatSort } from "@angular/material";
import { VehicleFeatureService } from "src/app/service/vehicle-feature.service";
import { NgxSpinnerService } from "ngx-spinner";
import { AppSettings } from "src/app/service/support/app.settings";
import { FeatureNameModel } from "src/app/service/support/feature.name.model";
import { EditFeatureComponent } from "./edit-feature/edit-feature.component";

@Component({
  selector: "app-vehicle-feature-management",
  templateUrl: "./vehicle-feature-management.component.html",
  styleUrls: ["./vehicle-feature-management.component.scss"]
})
export class VehicleFeatureManagementComponent implements OnInit {
  tableData = new MatTableDataSource();
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  displayedColumns: string[] = [
    "feature_name",
    "feature_description",
    "status",
    "edit"
  ];
  constructor(
    private fatureService: VehicleFeatureService,
    private spinner: NgxSpinnerService,
    private snackBar: MatSnackBar,
    public dialog: MatDialog
  ) {}
  applyFilter(filterValue: string) {
    this.tableData.filter = filterValue.trim().toLowerCase();

    if (this.tableData.paginator) {
      this.tableData.paginator.firstPage();
    }
  }
  openEditDialog(featureName: FeatureNameModel): void {
    const dialogRef = this.dialog.open(EditFeatureComponent, {
      width: "400px",
      data: featureName
    });

    dialogRef.afterClosed().subscribe(result => {
      this.fetchfFeatureNames();
    });
  }
  ngOnInit() {
    this.fetchfFeatureNames();
  }
  getStatusButtonState(state: string) {
    return state === "Active" ? true : false;
  }

  deleteFeatureName(model: FeatureNameModel) {
    this.spinner.show();
    this.fatureService.delete(model).subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.snackBar.open("Deleted " + model.FeatureName);
          this.fetchfFeatureNames();
        } else {
          this.snackBar.open(response.ErrorMessage);
        }
      },
      error => {
        this.spinner.hide();
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
      }
    );
  }
  changeStateOfFeature(state: boolean, model: FeatureNameModel) {
    if (state) {
      this.fatureService.active(model).subscribe(
        response => {
          if (response.Status) {
            this.snackBar.open(model.FeatureName + " Activated");
            this.fetchfFeatureNames();
          } else {
            this.snackBar.open(response.ErrorMessage);
            this.fetchfFeatureNames();
          }
        },
        error => {
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          this.fetchfFeatureNames();
        }
      );
    } else {
      this.fatureService.inactive(model).subscribe(
        response => {
          if (response.Status) {
            this.snackBar.open(model.FeatureName + " Deactivated");
            this.fetchfFeatureNames();
          } else {
            this.snackBar.open(response.ErrorMessage);
            this.fetchfFeatureNames();
          }
        },
        error => {
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          this.fetchfFeatureNames();
        }
      );
    }
  }
  fetchfFeatureNames() {
    this.spinner.show();
    this.fatureService.getAllFeatureNames().subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.tableData = new MatTableDataSource(response.Payload);
        } else {
          this.snackBar.open(response.ErrorMessage);
        }
      },
      error => {
        this.spinner.hide();
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
      }
    );
  }
}
