import { Component, OnInit } from "@angular/core";
import { UserViewModel } from "src/app/service/support/user.view.model";
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { UserService } from "src/app/service/user.service";
import { AppSettings } from "src/app/service/support/app.settings";
import { ActivatedRoute, Router } from "@angular/router";
import { NgxSpinnerService } from "ngx-spinner";
import { GlobalEventService } from "src/app/service/global.event.service";
import { MatSnackBar } from '@angular/material';

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"]
})
export class LoginComponent implements OnInit {
  user: UserViewModel = new UserViewModel();
  loginForm: FormGroup;
  private returnUrl: string;
  constructor(
    private globalEventService: GlobalEventService,
    private spinner: NgxSpinnerService,
    private formBuilder: FormBuilder,
    private userService: UserService,
    private route: ActivatedRoute,
    private router: Router,
    private snackBar: MatSnackBar
  ) {}
  keyDownFunction(event) {
    if (event.keyCode == 13) {
      this.login();
    }
  }
  ngOnInit() {
    this.userService.logout();
    this.globalEventService.triggerRegresh();
    this.returnUrl = this.route.snapshot.queryParams["returnUrl"] || "/home";

    this.loginForm = this.formBuilder.group({
      username: new FormControl("", [Validators.required]),
      password: new FormControl("", [Validators.required])
    });
  }

  login() {
    this.spinner.show();
    this.userService.login(this.user).subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          localStorage.setItem(
            AppSettings.AUTH_INFO_LOCATION,
            JSON.stringify(response.Payload)
          );
          if (response.Payload.UserRole == "Super User")
            this.router.navigate(["sudo"]);
          else this.router.navigate([this.returnUrl]);
        } else {
          this.snackBar.open(response.ErrorMessage);
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }
    );
  }
}
