import { Component, OnInit } from "@angular/core";
import { CountryService } from "src/app/service/country.service";
import { CountryViewModel } from "src/app/service/support/country.view.model";
import { AppSettings } from "src/app/service/support/app.settings";
import { Router } from "@angular/router";
import { GlobalEventService } from 'src/app/service/global.event.service';
import { MatSnackBar } from '@angular/material';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: "app-country-selector",
  templateUrl: "./country-selector.component.html",
  styleUrls: ["./country-selector.component.scss"]
})
export class CountrySelectorComponent implements OnInit {
  countries: CountryViewModel[] = [];
  constructor(private spinner:NgxSpinnerService, private snackBar:MatSnackBar,private countryService: CountryService, private router: Router,globlaEventService:GlobalEventService) {
    localStorage.removeItem(AppSettings.COUNTRY_INFO);
    globlaEventService.triggerRegresh();
  }
  setCountry(name) {
    localStorage.setItem(AppSettings.COUNTRY_INFO, name);
    this.router.navigate(["/site-home"]);
  }
  ngOnInit() {
    this.countryService.getCountries().subscribe(
      response => {
        if (response.Status) {
          this.countries = response.Payload;
        } else {
          console.log(response.ErrorMessage);
        }
      },
      error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }
    );
  }
}
