import { Component, OnInit } from '@angular/core';
import { YardViewModel } from 'src/app/service/support/yard.view.model';
import { YardService } from 'src/app/service/yard.service';
import { MatTableDataSource } from '@angular/material/table';

@Component({
  selector: 'app-yard',
  templateUrl: './yard.component.html',
  styleUrls: ['./yard.component.scss']
})
export class YardComponent implements OnInit {
  yards: YardViewModel[] = [];
  dataSource = new MatTableDataSource();
  displayedColumns: string[] = ['name',"suburb","state","edit"];
  constructor(private yardService: YardService) { }

  ngOnInit() {
    this.yardService.getUserYards().subscribe(
      response => {
        if (response.Status) {
          this.dataSource = new MatTableDataSource(response.Payload);
        }
      }, error => {
        console.log(error);
      }
    );
  }
  encodId(id) {
    const i = encodeURI(id);;
    return i;
  }

}
