import { Component, OnInit } from '@angular/core';
import { UserViewModel } from 'src/app/service/support/user.view.model';
import { FormBuilder, FormGroup, FormControl, Validators, FormGroupDirective, NgForm } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { UserService } from 'src/app/service/user.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ConfirmPasswordErrorStateMatcher } from 'src/app/service/support/error.state.matcher';
import { MatSnackBar } from '@angular/material';
import { AppSettings } from 'src/app/service/support/app.settings';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {
  user: UserViewModel = new UserViewModel();
  matcher = new ConfirmPasswordErrorStateMatcher();
  confirmPassword: string;

  pwresetForm: FormGroup;
  constructor(private snackBar: MatSnackBar,private spinner: NgxSpinnerService, private userService: UserService, private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.pwresetForm = this.formBuilder.group({
      currentPassword: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required]),
      confirmPassword: new FormControl('', [Validators.required])
    }, { validators: this.checkPasswords });
  }
  reset() {
  if(this.pwresetForm.hasError("notSame")){
    return;
  }
    if (this.pwresetForm.valid) {
      this.spinner.show();
      this.userService.resetPassword(this.user).subscribe(
        response => {
          this.spinner.hide();
          if (response.Status) {
            this.spinner.hide();
            this.snackBar.open("Password changed");
            this.pwresetForm.reset();
          } else {
            this.snackBar.open(response.ErrorMessage);
          }
        }, error => {
          console.log(error);
          this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
          this.spinner.hide();
        }
      );
    } else {
      this.pwresetForm.clearValidators();
    }
  }
  checkPasswords(group: FormGroup) {
    let pass = group.controls.password.value;
    let confirmPass = group.controls.confirmPassword.value;
    return pass === confirmPass ? null : { notSame: true }
  }
}
