import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { CommonResponse } from './support/common.response';
import { Server } from 'selenium-webdriver/safari';
import { ServerManager } from './support/server.manager';
import { AppSettings } from './support/app.settings';

@Injectable({
  providedIn: 'root'
})
export class VehicleConditionService {

  constructor(private http:HttpClient) { }
  getAll():Observable<CommonResponse>{
    return this.http.get<CommonResponse>(ServerManager.getEndpoint(AppSettings.VEHICLE_CONDITION_URL),ServerManager.getHeadders());
  }
}
