import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { RegisterComponent } from "./components/user/register/register.component";
import { LoginComponent } from "./components/user/login/login.component";
import { HomeComponent } from "./components/home/home.component";
import { HomeGuard } from "./guards/home.guard";
import { ProfileComponent } from "./components/user/profile/profile.component";
import { LoginGuard } from "./guards/Login.guard";
import { ResetPasswordComponent } from "./components/user/reset-password/reset-password.component";
import { DashboardComponent } from "./components/home/dashboard/dashboard.component";
import { YardComponent } from "./components/user/yard/yard.component";
import { AddYardComponent } from "./components/user/yard/add-yard/add-yard.component";
import { EditYardComponent } from "./components/user/yard/edit-yard/edit-yard.component";
import { SettingsComponent } from "./components/user/settings/settings.component";
import { ProfileHomeComponent } from "./components/user/profile/profile-home/profile-home.component";
import { CountrySelectorComponent } from "./components/country-selector/country-selector.component";
import { CountryGuard } from "./guards/country.guard";
import { SiteHomeComponent } from "./components/site-home/site-home.component";
import { SiteHomeGuard } from "./guards/site-home.guard";
import { RecoverPasswordComponent } from "./components/user/recover-password/recover-password.component";
import { DoRecoverPasswordComponent } from "./components/user/do-recover-password/do-recover-password.component";
import { InventoryComponent } from "./components/inventory/inventory.component";
import { VehicleTypeComponent } from "./components/inventory/vehicle-type/vehicle-type.component";
import { SudoComponent } from "./components/sudo/sudo.component";
import { SudoGuard } from "./guards/sudo.guard";
import { VehicalMakeMangementComponent } from "./components/sudo/vehical-make-mangement/vehical-make-mangement.component";
import { VehicleModelManagementComponent } from "./components/sudo/vehicle-model-management/vehicle-model-management.component";
import { VehicleFeatureManagementComponent } from "./components/sudo/vehicle-feature-management/vehicle-feature-management.component";
import { AddFeatureComponent } from "./components/sudo/vehicle-feature-management/add-feature/add-feature.component";
import { EditFeatureComponent } from "./components/sudo/vehicle-feature-management/edit-feature/edit-feature.component";
import { AddVehicleComponent } from "./components/inventory/add-vehicle/add-vehicle.component";
import { InStockComponent } from "./components/inventory/in-stock/in-stock.component";
import { OnOfferComponent } from "./components/inventory/on-offer/on-offer.component";
import { SoldComponent } from "./components/inventory/sold/sold.component";
import { ReturnedComponent } from "./components/inventory/returned/returned.component";
import { InTransistComponent } from "./components/inventory/in-transist/in-transist.component";
import { ViewVehicleComponent } from "./components/inventory/view-vehicle/view-vehicle.component";

const routes: Routes = [
  { path: "do-recover-password", component: DoRecoverPasswordComponent },
  { path: "recover-password", component: RecoverPasswordComponent },
  {
    path: "site-home",
    component: SiteHomeComponent,
    canActivate: [SiteHomeGuard]
  },
  {
    path: "country",
    component: CountrySelectorComponent,
    canActivate: [CountryGuard]
  },
  {
    path: "register",
    component: RegisterComponent,
    canActivate: [SiteHomeGuard]
  },
  {
    path: "sudo",
    component: SudoComponent,
    canActivate: [SudoGuard],
    children: [
      {
        path: "make-management",
        component: VehicalMakeMangementComponent,
        canActivate: [SudoGuard]
      },
      {
        path: "model-management",
        component: VehicleModelManagementComponent,
        canActivate: [SudoGuard]
      },
      {
        path: "feature-management",
        component: VehicleFeatureManagementComponent,
        canActivate: [SudoGuard]
      },
      {
        path: "add-feature",
        component: AddFeatureComponent,
        canActivate: [SudoGuard]
      }
    ]
  },
  { path: "login", component: LoginComponent, canActivate: [LoginGuard] },

  {
    path: "home",
    component: HomeComponent,
    canActivate: [HomeGuard],
    children: [
      {
        path: "inventory",
        component: InventoryComponent,
        canActivate: [HomeGuard],
        children: [
          // { path: "home/:id", component: InventoryHome, canActivate: [HomeGuard] },
          {
            path: "in-stock",
            component: InStockComponent,
            canActivate: [HomeGuard]
          },
          {
            path: "on-offer",
            component: OnOfferComponent,
            canActivate: [HomeGuard]
          },
          {
            path: "sold",
            component: SoldComponent,
            canActivate: [HomeGuard]
          },
          {
            path: "returned",
            component: ReturnedComponent,
            canActivate: [HomeGuard]
          },
          {
            path: "in-transist",
            component: InTransistComponent,
            canActivate: [HomeGuard]
          },
          {
            path: "view-vehicle/:id",
            component: ViewVehicleComponent,
            canActivate: [HomeGuard]
          },
          {
            path: "vehicle-type",
            component: VehicleTypeComponent,
            canActivate: [HomeGuard]
          },
          {
            path: "add-vehicle",
            component: AddVehicleComponent,
            canActivate: [HomeGuard]
          }
        ]
      },
      { path: "", component: DashboardComponent, canActivate: [HomeGuard] },
      {
        path: "profile",
        component: ProfileHomeComponent,
        canActivate: [HomeGuard],
        children: [
          {
            path: "my-profile",
            component: ProfileComponent,
            canActivate: [HomeGuard]
          }
        ]
      },
      {
        path: "reset_password",
        component: ResetPasswordComponent,
        canActivate: [HomeGuard]
      },
      {
        path: "settings",
        component: SettingsComponent,
        canActivate: [HomeGuard],
        children: [
          {
            path: "my_yards",
            component: YardComponent,
            canActivate: [HomeGuard]
          },
          {
            path: "add_yard",
            component: AddYardComponent,
            canActivate: [HomeGuard]
          },
          {
            path: "edit_yard/:id",
            component: EditYardComponent,
            canActivate: [HomeGuard]
          }
        ]
      }
    ]
  },

  { path: "**", redirectTo: "site-home" }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule {}
