import { Component, OnInit } from '@angular/core';
import { UserViewModel } from 'src/app/service/support/user.view.model';
import { UserService } from 'src/app/service/user.service';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { AppSettings } from 'src/app/service/support/app.settings';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  user: UserViewModel = new UserViewModel();
  editProfileForm: FormGroup;
  constructor(private snackBar: MatSnackBar,private spinner: NgxSpinnerService, private formBuilder: FormBuilder, private userService: UserService) { }

  ngOnInit() {

    this.editProfileForm = this.formBuilder.group({
      title: new FormControl(''),
      firstName: new FormControl('', [Validators.required]),
      lastName: new FormControl('', [Validators.required]),
      email: new FormControl('', [Validators.required,Validators.email]),
      mobileNumber: new FormControl('', [Validators.required]),
      smsOutMyMobile: new FormControl(),
      receiveLeadsViaSms: new FormControl(),
      enableChat: new FormControl(),
      bioText: new FormControl(),
      emailIncommingServerAddress: new FormControl(),
      emailOutgoingServerAddress: new FormControl(),
      emailIncommingServerPassword: new FormControl(),
      emailOutgoingServerPassword: new FormControl(),
      sendDailyActivityReport: new FormControl(),
    });
    this.spinner.show();
    this.userService.loginUserDetails().subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.user = response.Payload;
        }
      }, error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }

    );
  }

  update() {
    if(this.editProfileForm.valid){
    this.spinner.show();
    this.userService.updateUserDetails(this.user).subscribe(
      response => {
        this.spinner.hide();
        if (response.Status) {
          this.user = response.Payload;
          this.snackBar.open("Information updated");
        } else {
          this.snackBar.open("Error occurred while updating information");
        }
      }, error => {
        this.snackBar.open(AppSettings.COMMUNICATION_ERROR);
        console.log(error);
        this.spinner.hide();
      }
    );}else{
      this.editProfileForm.clearValidators();
    }
  }

}
