import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { YardViewModel } from "./support/yard.view.model";
import { Observable } from "rxjs";
import { CommonResponse } from "./support/common.response";
import { ServerManager } from "./support/server.manager";
import { AppSettings } from "./support/app.settings";

@Injectable({
  providedIn: "root"
})
export class YardService {
  constructor(private http: HttpClient) { }
  addYard(yard: YardViewModel): Observable<CommonResponse> {
    return this.http.post<CommonResponse>(
      ServerManager.getEndpoint(AppSettings.NEW_YARD_URL),
      yard,
      ServerManager.getHeadders()
    );
  }
  getYardFromId(yardId: string): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(ServerManager.getEndpoint(AppSettings.YARD_FROM_ID_URL(yardId)), ServerManager.getHeadders());
  }
  getUserYards(): Observable<CommonResponse> {
    return this.http.get<CommonResponse>(ServerManager.getEndpoint(AppSettings.USER_YARDS_URL), ServerManager.getHeadders());
  }
updateYard(yard:YardViewModel):Observable<CommonResponse>{
  return this.http.put<CommonResponse>(ServerManager.getEndpoint(AppSettings.UPDATE_YARD_URL),yard,ServerManager.getHeadders());
}
}
