import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanActivate, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { AppSettings } from '../service/support/app.settings';

@Injectable({
  providedIn: 'root'
})
export class SudoGuard implements CanActivate {
  constructor(private router: Router) { }
  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    if (localStorage.getItem(AppSettings.AUTH_INFO_LOCATION)) {
     if (JSON.parse(localStorage.getItem(AppSettings.AUTH_INFO_LOCATION)).UserRole=="Super User")
      return true;
      else
      return false;
    }

    return false;
  }
}
